import M from "materialize-css";
import apiKey from "../apiKey";
import axios from "axios";
import { storage } from "../helpers/storage";
import { store } from "../index";
import { registration } from "../redux/actions/actions";

export const signInUpHandler = async (loginValue, passValue, isSignUp) => {
  let url;

  if (isSignUp) {
    url = "https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=";
  } else {
    url =
      "https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=";
  }

  const authData = {
    email: loginValue,
    password: passValue,
    returnSecureToken: true,
  };

  try {
    const resp = await axios.post(`${url}${apiKey}`, authData);
    if (resp.status === 200) {
      console.log(resp);

      const data = resp.data;
      const expirationDate = new Date(
        new Date().getTime() + data.expiresIn * 1000
      );
      storage("token", data.idToken);
      storage("userId", data.localId);
      storage("expirationDate", expirationDate);

      if (isSignUp) {
        M.toast({ html: "Account created successfully" });
      } else {
        M.toast({ html: "Sign in was successful!" });
      }

      store.dispatch(registration(data.idToken));
    }
  } catch (e) {
    M.toast({ html: "Error" });
  }
};
