import React from "react";
import { TextField, Button } from "@material-ui/core";
import "./Form.css";
import { connect } from "react-redux";
import { signInUpHandler } from "../sign-in-up/signInUpHandler";
import { login_change, pass_change } from "../redux/actions/actions";

function Form(props) {
  return (
    <div className="form-wrapper">
      <div className="formInputs">
        <TextField
          label="Login"
          value={props.loginValue}
          onChange={(e) => props.loginChangehandler(e.target.value)}
        />
        <TextField
          label="Password"
          value={props.passValue}
          onChange={(e) => props.passChangehandler(e.target.value)}
        />
      </div>

      <div className="formButtons">
        <Button
          onClick={() =>
            signInUpHandler(props.loginValue, props.passValue, true)
          }
          variant="contained"
          color="primary"
        >
          sign up
        </Button>
        <Button
          onClick={() =>
            signInUpHandler(props.loginValue, props.passValue, false)
          }
          variant="contained"
          color="secondary"
        >
          sign in
        </Button>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  loginValue: state.loginValue,
  passValue: state.passValue,
});

const mapDispatchToProps = (dispatch) => ({
  loginChangehandler: (value) => dispatch(login_change(value)),
  passChangehandler: (value) => dispatch(pass_change(value)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Form);
