import { storage } from "../helpers/storage";

import {
  LOGIN_CHANGE,
  PASS_CHANGE,
  REGISTRATION,
  LOGOUT,
} from "./actions/actionTypes";

const initialState = {
  loginValue: "",
  passValue: "",
  token: null,
};

if (storage("token")) {
  initialState.token = storage("token");
}

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case LOGIN_CHANGE:
      return { ...state, loginValue: action.value };
    case PASS_CHANGE:
      return { ...state, passValue: action.value };
    case REGISTRATION:
      return {
        ...state,
        token: action.token,
        loginValue: "",
        passValue: "",
      };
    case LOGOUT:
      return {
        ...state,
        token: null,
      };
    default:
      return state;
  }
}
