import { LOGIN_CHANGE, PASS_CHANGE, REGISTRATION, LOGOUT } from "./actionTypes";

export function login_change(value) {
  return {
    type: LOGIN_CHANGE,
    value,
  };
}

export function pass_change(value) {
  return {
    type: PASS_CHANGE,
    value,
  };
}

export function registration(token) {
  return {
    type: REGISTRATION,
    token,
  };
}

export function logout() {
  return {
    type: "LOGOUT",
  };
}
